<template>
        <div class="city_container">
            <header id="head_top">
                <section class="head_goback">
                    <router-link to="/home">
                        <i class="el-icon-arrow-left"></i>
                    </router-link>
                </section>
                <section class="title_head ellipsis">
                    <span>{{cityInfo ? cityInfo.name : ""}}</span>
                </section>
                <router-link to="/home">切换城市</router-link>
            </header>
            <div class="city_form">
                <div>
                    <input type="search" placeholder="输入学校、商务楼、地址" class="city_input input_style" v-model="inputV">
                </div>
                <div>
                    <input type="submit" placeholder="提交" class="city_submit input_style" @click="searchPlace">
                </div>
            </div>
            <header class="pois_search_history" v-if="isShowHistoryHeader">搜索历史</header>
            <!--将来展示搜索内容&&历史记录-->
            <ul class="getpois_ul">
                <li v-for="p in placeArr" :key="p.geohash" @click="choosePlace(p)">
                    <h4 class="pois_name ellipsis">{{p.name}}</h4>
                    <p class="pois_address ellipsis">{{p.address}}</p>
                </li>
            </ul>
            <div class="clear_all_history" v-if="isShowClearBtn" @click="clearAllPlace">清空所有</div>
        </div>
</template>

<script>
    export default {
        name: "City",
        data(){
            return {
                cityId:0,
                cityInfo:null,
                inputV:"",
                placeArr:[],
                //控制搜索历史的显隐
                isShowHistoryHeader:true,
                //控制清楚历史按钮的显隐
                isShowClearBtn:false,
                cateBtn:[],

            }
        },
        created(){
             //先获取params传递来的城市id,根据id发起网络请求获取城市信息
            this.cityId = this.$route.params.cityId;
            // this.cityId = localStorage.getItem("cityId")
            this.axios.get("/v1/cities/"+this.cityId).then(res=>{
                console.log(res);//通过id请求到的城市
                this.cityInfo = res;
            }).catch(err=>{});
            //进入该页面时，查看是否有历史记录，如果有，展示
            if (localStorage.getItem("placeHistory")) {
                this.placeArr = JSON.parse(localStorage.getItem("placeHistory"));
                //
                this.isShowClearBtn = true;
            }
        },
        methods:{
            searchPlace(){
                this.axios.get("/v1/pois",{
                    params:{
                        city_id:this.cityInfo.id,
                        keyword:this.inputV,
                        type:"search"
                    }
                }).then(res=>{
                    // console.log(res);
                    this.placeArr = res;
                    this.isShowHistoryHeader = false;
                }).catch(err=>{
                    // console.log(err);
                })
            },
            choosePlace(p){
                console.log(p);
                /*
                   1.第一次存，先创建一个数组，将p放进数组，将数组存储在localStroage
                   2.以后存 获取数组，将p放进数组 将数组存储localStorage
                 */
                if (!localStorage.getItem("placeHistory")){
                    //第一次存
                    let hisArr = [];
                    hisArr.push(p);
                    localStorage.setItem("placeHistory",JSON.stringify(hisArr));
                } else{
                    //之后存
                    let hisArr = JSON.parse(localStorage.getItem("placeHistory"));

                    //去重逻辑  自己写第一种
                    // const obj = {};
                    // for(let i = 0; i < p.length; i++) {
                    //     obj[p[i].p.name] = 1;
                    // }
                    // console.log(obj);
                    // hisArr.push(p);
                    // localStorage.setItem("placeHistory",JSON.stringify(hisArr));

                    //去重第二中
                    let newArr = hisArr.filter(v=>{
                       return v.name == p.name;
                    });
                    if (!newArr.length){
                    hisArr.push(p);
                    localStorage.setItem("placeHistory",JSON.stringify(hisArr));
                    }

                }
                localStorage.setItem("geohashId",p.geohash);
                localStorage.setItem("cityName",p.name);
                // this.$router.push({name:'b'});
                // this.$router.push({name:'b',params:{pName:p.name}});
                this.$router.push({path:'/msite'})
                // this.$router.push({path:"/msite", query:{geohash:address.geohash}}).catch(err=>{});
                // this.$router.push({name:'b',params:{pName:p.geohash}})
                // this.$router.push({path:"/msite",query:{username:p.geohash}})
            },
            clearAllPlace(){
                //点击清楚所有，删除placeHistory,清空下拉列表，隐藏清空按钮
                localStorage.removeItem("placeHistory");
                this.placeArr = [];
                this.isShowClearBtn = false;
            }
        }
    }
</script>

<style scoped>
    .city_container{
        padding-top: 2.35rem;
    }
    #head_top {
        background-color: #3190e8;
        position: fixed;
        z-index: 100;
        left: 0;
        top: 0;
        width: 100%;
        height: 1.95rem;
    }
    .head_goback{
        left: .4rem;
        width: .6rem;
        height: 1rem;
        line-height: 2.2rem;
        margin-left: .4rem;
    }
    .el-icon-arrow-left{
        color: #fff;
    }
    .title_head{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 50%;
        color: #fff;
        text-align: center;
    }
    header>a{
        text-decoration: none;
        right: .4rem;
        font-size: .6rem;
        color: #fff;
        position: absolute;
        top: 50%;
        transform: translateY(-50%);
    }
    .city_form{
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-top: .4rem;
    }
    .city_form div{
        width: 90%;
        margin: 0 auto;
        text-align: center;
    }
    .input_style{
        border-radius: .1rem;
        margin-bottom: .4rem;
        width: 100%;
        height: 1.4rem;
    }
    .city_input{
        border: 1px solid #e4e4e4;
        padding: 0 .3rem;
        font-size: .65rem;
        color: #333;
    }
    .city_submit{
        background-color: #3190e8;
        font-size: .65rem;
        color: #fff;
    }
    .pois_search_history{
        border-top: 1px solid #e4e4e4;
        border-bottom: 1px solid #e4e4e4;
        padding-left: .5rem;
        font: .475rem/.8rem Microsoft YaHei;
    }
    .getpois_ul{
        background-color: #fff;
        border-top: 1px solid #e4e4e4;
    }
    .getpois_ul li{
        margin: 0 auto;
        padding-top: .65rem;
        border-bottom: 1px solid #e4e4e4;
    }
    .pois_name{
        margin: 0 auto .35rem;
        width: 90%;
        font-size: .65rem;
        color: #333;
    }
    .pois_address{
        width: 90%;
        margin: 0 auto .55rem;
        font-size: .45rem;
        color: #999;
    }
    .clear_all_history{
        font-size: .7rem;
        color: #666;
        text-align: center;
        line-height: 2rem;
        background-color: #fff;
    }
    .ellipsis{
        /*文字不换行*/
        white-space: nowrap;
        /*操出部分隐藏*/
        overflow: hidden;
        /*用点代替*/
        text-overflow: ellipsis;
    }
</style>